/**
 * API utility functions for authentication
 */

interface LoginCredentials {
  username: string
  password: string
  rememberMe: boolean
}

interface LoginResponse {
  success: boolean
  message?: string
  user?: any
  redirect?: string
}

/**
 * Login user with credentials
 * This function will submit the login form to Moodle's existing login endpoint
 */
export async function loginUser(credentials: LoginCredentials): Promise<LoginResponse> {
  try {
    // Get the login token from Moodle's login page
    const loginToken = await getLoginToken()

    // Create form data
    const formData = new FormData()
    formData.append('username', credentials.username.toLowerCase().trim())
    formData.append('password', credentials.password)
    formData.append('rememberusername', credentials.rememberMe ? '1' : '0')
    formData.append('logintoken', loginToken)

    // Submit to Moodle's login endpoint
    const response = await fetch('/login/index.php', {
      method: 'POST',
      body: formData,
      credentials: 'same-origin',
      headers: {
        'Accept': 'text/html,application/xhtml+xml,application/xml'
      }
    })

    // Check if login was successful by checking redirect or response
    if (response.redirected) {
      return {
        success: true,
        redirect: response.url
      }
    }

    // Parse the response text to check for errors
    const responseText = await response.text()

    // Check for common error messages
    if (responseText.includes('Invalid login') || responseText.includes('invalidlogin')) {
      return {
        success: false,
        message: 'Invalid username or password. Please try again.'
      }
    }

    if (responseText.includes('Too many login attempts')) {
      return {
        success: false,
        message: 'Too many failed login attempts. Please try again later.'
      }
    }

    // If we reach here and no redirect occurred, consider it a failure
    return {
      success: false,
      message: 'Login failed. Please check your credentials and try again.'
    }

  } catch (error: any) {
    console.error('Login error:', error)
    return {
      success: false,
      message: error.message || 'An error occurred during login. Please try again.'
    }
  }
}

/**
 * Login as guest
 */
export async function loginGuest(): Promise<LoginResponse> {
  try {
    const response = await fetch('/login/index.php?guest=1', {
      method: 'GET',
      credentials: 'same-origin',
      redirect: 'follow'
    })

    if (response.redirected || response.ok) {
      return {
        success: true,
        redirect: response.url || '/'
      }
    }

    return {
      success: false,
      message: 'Guest login is not available at this time.'
    }
  } catch (error: any) {
    console.error('Guest login error:', error)
    return {
      success: false,
      message: 'An error occurred during guest login.'
    }
  }
}

/**
 * Get login token from Moodle's session
 * This token is required for CSRF protection
 */
async function getLoginToken(): Promise<string> {
  try {
    const response = await fetch('/login/index.php', {
      method: 'GET',
      credentials: 'same-origin'
    })

    const html = await response.text()

    // Extract logintoken from the HTML
    const tokenMatch = html.match(/name="logintoken"\s+value="([^"]+)"/)

    if (tokenMatch && tokenMatch[1]) {
      return tokenMatch[1]
    }

    // If no token found, return empty string
    // Moodle will handle the missing token appropriately
    return ''
  } catch (error) {
    console.error('Error getting login token:', error)
    return ''
  }
}
