<template>
  <div class="login-page-exact">
    <!-- Page Banner (matching Moodle design) -->
    <div class="page-banner-area bg-secondary py-60 position-relative z-1 overflow-hidden">
      <div class="container">
        <div class="row align-items-center">
          <div class="col-lg-6 col-sm-6">
            <div class="page-banner-content text-sm-start text-center mb-4 mb-sm-0">
              <h2 class="fs-2 text-white mb-md-3 mb-1">Log in to the site</h2>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- Login Area (matching Moodle design exactly) -->
    <div class="login-area">
      <div class="container">
        <div class="row">
          <div class="col-12">
            <div class="login-wrapper pt-100 pb-75">
              <div class="login-container">
                <div class="loginform">
                  <!-- Login Logo -->
                  <div id="loginlogo" class="login-logo">
                    <img
                      id="logoimage"
                      src="https://dev.bheem.cloud/pluginfile.php?file=%2F1%2Fcore_admin%2Flogo%2F0x200%2F-1%2Flogo.png"
                      class="img-fluid"
                      alt="Bheem Academy : : AI-Driven Learning for a Smarter"
                    />
                    <h1 class="login-heading visually-hidden">
                      Log in to Bheem Academy : : AI-Driven Learning for a Smarter
                    </h1>
                  </div>

                  <!-- Login Form -->
                  <form
                    class="login-form"
                    @submit.prevent="handleLogin"
                    method="post"
                    id="login"
                    novalidate
                  >
                    <input id="anchor" type="hidden" name="anchor" value="" />

                    <!-- Username Field -->
                    <div class="login-form-username mb-3">
                      <label for="username" class="visually-hidden">Username</label>
                      <input
                        type="text"
                        name="username"
                        id="username"
                        v-model="formData.username"
                        class="form-control form-control-lg"
                        :class="{ 'is-invalid': errors.username }"
                        placeholder="Username"
                        autocomplete="username"
                        @blur="validateField('username')"
                      />
                      <div v-if="errors.username" class="invalid-feedback">
                        {{ errors.username }}
                      </div>
                    </div>

                    <!-- Password Field -->
                    <div class="login-form-password mb-3">
                      <label for="password" class="visually-hidden">Password</label>
                      <input
                        :type="showPassword ? 'text' : 'password'"
                        name="password"
                        id="password"
                        v-model="formData.password"
                        class="form-control form-control-lg"
                        :class="{ 'is-invalid': errors.password }"
                        placeholder="Password"
                        autocomplete="current-password"
                        @blur="validateField('password')"
                      />
                      <div v-if="errors.password" class="invalid-feedback">
                        {{ errors.password }}
                      </div>
                    </div>

                    <!-- Error Message -->
                    <div v-if="loginError" class="alert alert-danger" role="alert">
                      {{ loginError }}
                    </div>

                    <!-- Submit Button -->
                    <div class="login-form-submit mb-3">
                      <button
                        class="btn btn-primary btn-lg"
                        type="submit"
                        id="loginbtn"
                        :disabled="isLoading"
                      >
                        {{ isLoading ? 'Logging in...' : 'Log in' }}
                      </button>
                    </div>

                    <!-- Forgot Password Link -->
                    <div class="login-form-forgotpassword mb-3">
                      <a href="/login/forgot_password.php">Lost password?</a>
                    </div>
                  </form>

                  <!-- Divider -->
                  <div class="login-divider"></div>

                  <!-- Guest Login -->
                  <h2 class="login-heading">Some courses may allow guest access</h2>
                  <form @submit.prevent="handleGuestLogin" id="guestlogin">
                    <button
                      class="btn btn-secondary"
                      type="submit"
                      id="loginguestbtn"
                      :disabled="isLoading"
                    >
                      Access as a guest
                    </button>
                  </form>

                  <!-- Divider -->
                  <div class="login-divider"></div>

                  <!-- Cookies Notice -->
                  <div class="d-flex">
                    <button
                      type="button"
                      class="btn btn-secondary"
                      @click="showCookiesNotice"
                    >
                      Cookies notice
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue'
import { useAuth } from '../../composables/useAuth'
import { validateUsername, validatePassword } from '../../utils/validation'

const { login, loginAsGuest } = useAuth()

const formData = reactive({
  username: '',
  password: '',
  rememberMe: false
})

const errors = reactive({
  username: '',
  password: ''
})

const showPassword = ref(false)
const isLoading = ref(false)
const loginError = ref('')

onMounted(() => {
  // Set anchor value from hash
  const anchorInput = document.getElementById('anchor') as HTMLInputElement
  if (anchorInput) {
    anchorInput.value = location.hash
  }
})

const validateField = (field: 'username' | 'password') => {
  if (field === 'username') {
    errors.username = validateUsername(formData.username)
  } else if (field === 'password') {
    errors.password = validatePassword(formData.password)
  }
}

const validateForm = (): boolean => {
  errors.username = validateUsername(formData.username)
  errors.password = validatePassword(formData.password)

  return !errors.username && !errors.password
}

const handleLogin = async () => {
  loginError.value = ''

  if (!validateForm()) {
    return
  }

  isLoading.value = true

  try {
    await login({
      username: formData.username,
      password: formData.password,
      rememberMe: formData.rememberMe
    })
  } catch (error: any) {
    loginError.value = error.message || 'Invalid login credentials. Please try again.'
  } finally {
    isLoading.value = false
  }
}

const handleGuestLogin = async () => {
  isLoading.value = true
  loginError.value = ''

  try {
    await loginAsGuest()
  } catch (error: any) {
    loginError.value = error.message || 'Guest login is not available.'
  } finally {
    isLoading.value = false
  }
}

const showCookiesNotice = () => {
  alert('Cookies notice: This site uses cookies to ensure you get the best experience on our website.')
}
</script>

